/*  */
.editButtonWrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

/*  */
.editButtonWrapper > p {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    cursor: pointer;
}

/*  */
.editButtonWrapper > p:hover {
    color: #3B90FF;
}

/*  */
.outlineMainWrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

/*  */
.topWrapper {
    width: 100%;
    margin-bottom: 0.9%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

/*  */
.selectWrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

/*  */
.tableWrapper {
    width: 100%;
    overflow-y: auto;
}

/*  */
#chapterMergedCell, #sectionMergedCell {
    background-color: rgba(112, 112, 112, 0.1) !important;
}

/*  */
#chapterWhiteCell, #sectionWhiteCell {
    background-color: white !important;
}