/*  */
.itemWrapper {
    width: 25%;
    height: 56%;
    margin-bottom: 4%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
}

/*  */
.infoCard {
    position: relative;
    width: 80%;
    height: 100%;
    margin-left: 5%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    border-radius: 5px;
    box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.15);

}

/*  */
.tag {
    position: absolute;
    top: 0;
    right: 0;
    border-top-right-radius: 5px;
    color: white;
    padding: 3px 7px;
}

/*  */
.infoCard > img {
    width: 100%;
    height: 54%;
}

/*  */
.textWrapper {
    width: 100%;
    height: 46%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}