/*  */
.ant-pagination-item-active {
    background-color: #67A1F3 !important;
    border: #67A1F3 !important;
}

/*  */
.ant-pagination-item-active > a {
    color: white !important;
}

/*  */
tr > th {
    background-color: #67A1F3 !important;
    color: white !important;
    border: none !important;
}

/*  */
tr > td, tr > th {
    padding: 9px 0 !important;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

/*  */
tr > th svg {
    color: white !important;
    width: 35px;
    margin-left: -25px;
}

/* .ant-table-filter-column {
    justify-content: center !important;
} */

/*  */
.odd_row {
    background-color: white;
}

/*  */
.even_row {
    background-color: rgba(112, 112, 112, 0.1);
}

/*  */
.ant-table-tbody > tr > td {
    background: rgba(255,255,255,0) !important;
}

/*  */
.ant-table-tbody > tr:hover{
    background: rgba(140, 188, 255, 0.5) !important;
}

.ant-table-tbody > tr:hover > td {
    border: 1px solid rgba(140, 188, 255, 0.5) !important;
}

/*  */
.ant-table-tbody td, th {
    border: 1px solid rgba(112, 112, 112, 0.2) !important;
}

/*  */
.showTotalWrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

/*  */
.showTotalWrapper > Button {
    border-color: #3B90FF;
    color: #3B90FF;
}