/*  */
.cardWrapper {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background-color: white;
    box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.15);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

/*  */
.cardDashedWrapper{
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 1px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

/*  */
.dashed {
    width: 95%;
    height: 1px;
    border-bottom: 1px dashed #DDDDDD;
}

/*  */
.leftCircleBg{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: #F2F2F2;
    position: absolute;
    top: -10px;
    left: -10px;
}

/*  */
.leftCircle{
    width: 10px;
    height: 20px;
    border-radius: 0 10px 10px 0;
    background: #F2F2F2;
    box-shadow: -1px 0px 1px rgba(0, 0, 0, 0.15) inset;
    position: absolute;
    top: -10px;
    left: 0;
}

/*  */
.rightCircleBg{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: #F2F2F2;
    position: absolute;
    top: -10px;
    right: -10px;
}

/*  */
.rightCircle{
    width: 10px;
    height: 20px;
    border-radius: 10px 0 0 10px;
    background: #F2F2F2;
    box-shadow: 2px 0px 1px rgba(0, 0, 0, 0.15) inset;
    position: absolute;
    top: -10px;
    right: 0;
}

/*  */
.courseTopWrapper {
    background-color: white;
    width: 100%;
    height: 25%;
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 0 10% 0 5%;
}

/*  */
.courseTopWrapper > img {
    height: 60%;
}

/*  */
.infoWrapper {
    width: 40%;
    height: 50%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-left: 4%;
}

/*  */
.infoLeft {
    width: 47.5%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}

/*  */
.infoRight {
    width: 47.5%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}

/*  */
.infoLeft > p, .infoRight > p {
    width: 100%;
    white-space: nowrap;
    overflow-x: auto;
}

/*  */
.buttonWrapper {
    /* background-color: rebeccapurple; */
    width: 30%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-start;
}

/*  */
.buttonItem {
    width: 20%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

/*  */
.buttonLine{
    background-color: #67A1F3;
    width: 1px;
    height: 25%;
}

/*  */
.buttonText {
    background-color: #67A1F3;
    width: 55%;
    height: 55%;
    border-radius: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

/*  */
.buttonText > p, .buttonTextPurple > p {
    color: white;
    writing-mode: vertical-lr;
    writing-mode: tb-lr;
    text-align: center;
}

/* - */
.buttonLinePurple{
    background-color: #8183FF;
    width: 1px;
    height: 25%;
}

/* - */
.buttonTextPurple {
    background-color: #8183FF;
    width: 55%;
    height: 55%;
    border-radius: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

/*  */
.TableWrapper {
    width: 90%;
    height: 75%;
    overflow-y: auto;
    margin-top: 1.5%;
    margin-bottom: 1%;
}

/*  */
.editButtonWrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

#changeGradeIcon {
    margin-right: 10%;
}

#changeGradeIcon, #outOfClassIcon {
    cursor: pointer;
}

#changeGradeIcon svg, #outOfClassIcon svg {
    color: #333 !important;
}

/*  */
.editButtonWrapper > p {
    white-space: nowrap;
    text-overflow:ellipsis;
    overflow:hidden;
}