/*  */
.mainWrapper {
    width: 100%;
    height: 100%;
    overflow-y: auto;
}

/*  */
.topLineWrapper {
    width: 100%;
    height: 5%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

/*  */
.buttonWrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
}

/*  */
.editButtonWrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

#editAgain, #changeStatus {
    margin-right: 10%;
}

#editAgain, #changeStatus, #exportPrinting {
    cursor: pointer;
}

#editAgain svg, #changeStatus svg, #exportPrinting svg {
    color: #333 !important;
}

/*  */
.editButtonWrapper > p {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

/*  */
.switchButton {
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: #3B90FF;
    padding: 5px 10px;
    border-radius: 0 0 5px 0;
    cursor: pointer;
}